/**
 * https://www.npmjs.com/package/cf-component-page
 *
 * <article className="cf-page">
 *   <header class="cf-page__header">
 *     <h1 class="cf-page__title">...</h1>
 *     <p class="cf-page__subtitle">...</p>
 *   </header>
 *   ...
 * </article>
 */

.cf-page {
  padding-top: calc((strip($cf-font-size) * 2.5) / $cf-em);
  padding-bottom: calc(20 / $cf-rem);
}

.cf-page__title {
  font-size: 2rem;
  line-height: 1.2;
  font-weight: 400;
}

.cf-page__subtitle {
  color: $cf-color-gray;
  font-weight: $cf-weight-light;
}

@media (min-width: $cf-breakpoint-tablet) {
  .cf-page {
    padding-bottom: calc(40 / $cf-rem);
  }
}
