/**
 * https://www.npmjs.com/package/cf-component-button
 *
 * <div class="cf-btn-group">
 *   <button class="cf-btn cf-btn--default">
 *     ...
 *   </button>
 *
 *   <button class="cf-btn cf-btn--primary">
 *     ...
 *   </button>
 *
 *   <!-- There can also be dropdowns -->
 *   <ul class="cf-dropdown cf-dropdown--left">
 *     ...
 *   </ul>
 * </div>
 */

.cf-btn__group {
  position: relative;
  display: inline-block;
  font-size: 0;
  vertical-align: middle;
  white-space: nowrap;

  & > * {
    font-size: 1rem;
  }

  & + & {
    margin-left: $cf-button-spacing;
  }

  .cf-btn {
    border-radius: 0;

    &:first-child {
      border-top-left-radius: $cf-button-border-radius;
      border-bottom-left-radius: $cf-button-border-radius;
    }

    &:last-child {
      border-top-right-radius: $cf-button-border-radius;
      border-bottom-right-radius: $cf-button-border-radius;
    }

    /**
     * Push the buttons together (offset the border)
     */
    & ~ .cf-btn {
      margin-left: -1px;
    }
  }
}

.cf-btn {
  display: inline-block;
  position: relative;
  margin: 0;
  padding: $cf-button-padding;

  border: none;
  border-bottom: 1px solid;
  background: transparent;
  border-radius: $cf-button-border-radius;

  font-size: $cf-button-font-size;
  font-weight: $cf-button-font-weight;
  font-family: inherit;
  line-height: 1.2; /* This is the default for input[type="button"] on FF, where you can't adjust it */
  text-align: center;
  -webkit-text-stroke: 0; /* This makes white text in buttons hard to read, so removing it */
  -webkit-font-smoothing: subpixel-antialiased;

  user-select: none;
  cursor: pointer;

  transition: all 200ms ease;

  &:focus {
    @extend %cf-a11y-tab_focus;
  }

  &::-moz-focus-inner {
    padding: 0;
    border: 0;
  }

  & + & {
    margin-left: $cf-button-spacing;
  }
}

/**
 * Themes
 */

.cf-btn--default {
  background-color: $cf-button-default-background;
  border-color: $cf-button-default-border;
  color: $cf-button-default-color;

  &:hover{
    background-color: $cf-button-default-hover-background;
    border-color: $cf-button-default-hover-border;
    color: $cf-button-default-hover-color;
  }

  &.cf-btn--active,
  &:focus,
  &:active {
    background-color: $cf-button-default-active-background;
    border-color: $cf-button-default-active-border;
    color: $cf-button-default-active-color;
  }
}

.cf-btn--primary {
  background-color: $cf-button-primary-background;
  border-color: $cf-button-primary-border;
  color: $cf-button-primary-color;

  &:hover{
    background-color: $cf-button-primary-hover-background;
    border-color: $cf-button-primary-hover-border;
    color: $cf-button-primary-hover-color;
  }

  &.cf-btn--active,
  &:focus,
  &:active {
    background-color: $cf-button-primary-active-background;
    border-color: $cf-button-primary-active-border;
    color: $cf-button-primary-active-color;
  }
}

.cf-btn--success {
  background-color: $cf-button-success-background;
  border-color: $cf-button-success-border;
  color: $cf-button-success-color;

  &:hover {
    background-color: $cf-button-success-hover-background;
    border-color: $cf-button-success-hover-border;
    color: $cf-button-success-hover-color;
  }

  &.cf-btn--active,
  &:focus,
  &:active {
    background-color: $cf-button-success-active-background;
    border-color: $cf-button-success-active-border;
    color: $cf-button-success-active-color;
  }
}

.cf-btn--warning {
  background-color: $cf-button-warning-background;
  border-color: $cf-button-warning-border;
  color: $cf-button-warning-color;

  &:hover {
    background-color: $cf-button-warning-hover-background;
    border-color: $cf-button-warning-hover-border;
    color: $cf-button-warning-hover-color;
  }

  &.cf-btn--active,
  &:focus,
  &:active {
    background-color: $cf-button-warning-active-background;
    border-color: $cf-button-warning-active-border;
    color: $cf-button-warning-active-color;
  }
}

.cf-btn--danger {
  background-color: $cf-button-danger-background;
  border-color: $cf-button-danger-border;
  color: $cf-button-danger-color;

  &:hover {
    background-color: $cf-button-danger-hover-background;
    border-color: $cf-button-danger-hover-border;
    color: $cf-button-danger-hover-color;
  }

  &.cf-btn--active,
  &:focus,
  &:active {
    background-color: $cf-button-danger-active-background;
    border-color: $cf-button-danger-active-border;
    color: $cf-button-danger-active-color;
  }
}

.cf-btn--disabled {
  cursor: default;
  pointer-events: none;
  opacity: 0.5;

  /*
  &,
  &:hover,
  &.cf-btn--active,
  &:focus,
  &:active{
    background-color: $cf-button-disabled-background;
    border-color: $cf-button-disabled-border;
    color: $cf-button-disabled-color;
  }
  */

 /**
  * Enable pointer events for disabled buttons with a title so that tooltips
  * display. Actions aren't triggered on buttons with the disabled class.
  */
  &[title] {
    pointer-events: auto;
  }
}

.cf-btn--link {
  background-color: transparent;
  border-color: transparent;
  color: $cf-link-default-color;

  &:hover {
    color: $cf-link-hover-color;
  }

  &.cf-btn--active,
  &:focus,
  &:active {
    color: $cf-link-default-color;
  }
}

/**
 * 3rd-party themes
 */

.cf-btn--twitter,
.cf-btn--facebook {
  i {
    vertical-align: baseline;
    font-size: calc((strip($cf-font-size) * 1.33) / $cf-em);
  }
}

.cf-btn--twitter {
  background-color: $cf-color-twitter-blue;
  border-color: $cf-color-twitter-blue;
  color: $cf-color-white;

  &:hover {
    background-color: color($cf-color-twitter-blue lightness(+10%));
    border-color: color($cf-color-twitter-blue lightness(+5%));
    color: $cf-color-gray-dark;
  }
}

.cf-btn--facebook {
  background-color: $cf-color-facebook-blue;
  border-color: $cf-color-facebook-blue;
  color: $cf-color-white;

  &:hover {
    background-color: color($cf-color-facebook-blue lightness(+10%));
    border-color: color($cf-color-facebook-blue lightness(+5%));
    color: $cf-color-white;
  }
}

/**
 * Actions
 */

 /**
  * Icon variants
  */

%cf-btn--action {
  min-height: calc((strip($cf-font-size) * 2.425) / $cf-rem);
  width: 0;
  overflow: hidden;
  padding: calc(17 / $cf-em);
  padding: calc(17 / $cf-em);

  text-align: left;
  text-indent: -9999px;
  white-space: nowrap;

  font-family: "cloudflare-font";
  font-weight: normal;
  font-style: normal;
  text-decoration: inherit;
  -webkit-font-smoothing: subpixel-antialiased;

  &::after {
    content: "\f009";
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 53%;
    left: 0;

    text-align: center;
    text-indent: 0;
    font-size: calc(strip($cf-font-size) * $cf-em);
    line-height: 0;

    speak: none;

    transition-delay: 200ms;
  }

  &::before,
  &::after {
    transition: opacity 200ms ease;
  }
}

.cf-btn--accept {
  @extend %cf-btn--action;
  @extend .cf-btn--success;

  &::after {
    content: "\f009";
  }
}

.cf-btn--cancel {
  @extend %cf-btn--action;
  @extend .cf-btn--danger;

  &::after{
    content: "\f01a";
  }
}

.cf-btn--delete {
  @extend %cf-btn--action;
  @extend .cf-btn--default;

  &::after{
    content: "\f01a";
  }
}

.cf-btn--edit {
  @extend %cf-btn--action;
  @extend .cf-btn--default;

  &::after{
    content: "\f019";
  }
}

.cf-btn--note {
  @extend %cf-btn--action;
  @extend .cf-btn--default;

  &::after{
    content: "\f013";
  }
}

/**
 * Sizes
 */

.cf-btn--large {
  /*padding: $fontSize/$rem 25px/$rem ($fontSize - 1)/$rem;*/
}

.cf-btn--xlarge {
  /*padding: $fontSize/$rem ($fontSize*3)/$rem;*/
}

.cf-btn--block {
  display: block;
  margin-bottom: $cf-button-spacing;

  & + & {
    margin-left: auto;
  }
}

/**
 * States
 */

.cf-btn--loading {
  color: $cf-button-disabled-background;
  background-color: $cf-button-disabled-background;
  opacity: 0.8;
  cursor: default;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;

    opacity: 1;
    background: transparent url(asset-url("image", "spinner.gif")) no-repeat center;

    transform: scale(1);
    animation: cf-btn-fade-zoom-in 200ms ease-in-out;
  }

  &:focus {
    outline: none;
  }

  &:hover,
  &:focus {
    background-color: $cf-button-disabled-background;
    color: $cf-button-disabled-background;
  }

}

/**
 * Fade & Zoom In
 */
@keyframes cf-btn-fade-zoom-in {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

