/**
 * https://www.npmjs.com/package/cf-component-tabs
 *
 * <section class="cf-tabs">
 *   <!-- Mobile -->
 *   <div class="cf-select">...</div>
 *
 *   <!-- Not mobile -->
 *   <ul class="cf-tabs__group" role="tablist">
 *     <li class="cf-tabs__item cf-tabs__item--active" role="tab">One</li>
 *     <li class="cf-tabs__item" role="tab">Two</li>
 *   </ul>
 *
 *   <!-- Tab panels are always in the dom, hidden/shown by styling -->
 *   <div class="cf-tabs__panel" style="display: block;" role="tabpanel">
 *     ...
 *   </div>
 *   <div class="cf-tabs__panel" style="display: none;" role="tabpanel">
 *     ...
 *   </div>
 * </section>
 */

.cf-tabs__group {
  display: table;
  width: 100%;
  margin: 0 0 $cf-tabs-spacing;
  padding: 0;
  list-style: none;
}

.cf-tabs__item {
  display: table-cell;
  position: relative;
  margin: 0;
  padding: $cf-tabs-padding;
  vertical-align: middle;
  text-align: center;

  background: $cf-tabs-background;
  color: $cf-tabs-color;

  cursor: pointer;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 100%;
    left: 50%;
    height: 0;
    width: 0;
    margin-left: 0;

    border: 0 solid transparent;
    border-top-color: $cf-tabs-active-background;

    transition: all 200ms ease;
  }

  & + & {
    border-left: 1px solid $cf-tabs-border;
  }

  &:focus {
    outline: none;

    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      @extend %cf-a11y-tab_focus;
    }
  }

  &:hover {
    background: $cf-tabs-hover-background;
    color: $cf-tabs-hover-color;
  }

  &:active,
  &.cf-tabs__item--active {
    background: $cf-tabs-active-background;
    color: $cf-tabs-active-color;

    &:before {
      border-width: $cf-tabs-arrow-size;
      margin-left: -$cf-tabs-arrow-size;
    }
  }
}
