/**
 * https://www.npmjs.com/package/cf-component-tooltip
 *
 * <!-- note there are a lot of extra classes for positioning -->
 * <div class="tooltip tooltip-open">
 *   <div class="tooltip-content">...</div>
 * </div>
 */

.tooltip {
  position: absolute;
  display: none;
  max-width: 100%;
  max-height: 100%;
  pointer-events: none;
}

.tooltip-open {
  display: block;
}

.tooltip-content {
  position: relative;
  padding: $cf-tooltip-padding;
  font-size: $cf-tooltip-font-size;
  font-family: inherit;
  background: $cf-tooltip-background;
  color: $cf-tooltip-color;
  border-radius: $cf-tooltip-border-radius;

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: solid transparent $cf-tooltip-arrow-size;
  }
}

/**
 *    -v-
 * +-------+
 * |       |
 * +-------+
 */
.tooltip-element-attached-bottom.tooltip-element-attached-center .tooltip-content {
  margin-bottom: $cf-tooltip-arrow-size;

  &::before {
    top: 100%;
    left: 50%;
    margin-left: -$cf-tooltip-arrow-size;
    border-top-color: $cf-tooltip-background;
  }
}

/**
 * +-------+
 * |       |
 * +-------+
 *    -^-
 */
.tooltip-element-attached-top.tooltip-element-attached-center .tooltip-content {
  margin-top: $cf-tooltip-arrow-size;

  &::before {
    bottom: 100%;
    left: 50%;
    margin-left: -$cf-tooltip-arrow-size;
    border-bottom-color: $cf-tooltip-background;
  }
}

/**
 *     +-------+
 * --> |       |
 *     +-------+
 */
.tooltip-element-attached-middle.tooltip-element-attached-right .tooltip-content {
  margin-right: $cf-tooltip-arrow-size;

  &::before {
    left: 100%;
    top: 50%;
    margin-top: -$cf-tooltip-arrow-size;
    border-left-color: $cf-tooltip-background;
  }
}

/**
 * +-------+
 * |       | <--
 * +-------+
 */
.tooltip-element-attached-middle.tooltip-element-attached-left .tooltip-content {
  margin-left: $cf-tooltip-arrow-size;

  &::before {
    right: 100%;
    top: 50%;
    margin-top: -$cf-tooltip-arrow-size;
    border-right-color: $cf-tooltip-background;
  }
}

/**
 * +-------+
 * |       |
 * +-------+
 * ^--
 */
.tooltip-element-attached-top.tooltip-element-attached-left.tooltip-target-attached-bottom .tooltip-content {
  margin-top: $cf-tooltip-arrow-size;

  &::before {
    bottom: 100%;
    left: $cf-tooltip-arrow-size;
    border-bottom-color: $cf-tooltip-background;
  }
}

/**
 * +-------+
 * |       |
 * +-------+
 *       --^
 */
.tooltip-element-attached-top.tooltip-element-attached-right.tooltip-target-attached-bottom .tooltip-content {
  margin-top: $cf-tooltip-arrow-size;

  &::before {
    bottom: 100%;
    right: $cf-tooltip-arrow-size;
    border-bottom-color: $cf-tooltip-background;
  }
}

/**
 * v--
 * +-------+
 * |       |
 * +-------+
 */
.tooltip-element-attached-bottom.tooltip-element-attached-left.tooltip-target-attached-top .tooltip-content {
  margin-bottom: $cf-tooltip-arrow-size;

  &::before {
    top: 100%;
    left: $cf-tooltip-arrow-size;
    border-top-color: $cf-tooltip-background;
  }
}

/**
 *       --v
 * +-------+
 * |       |
 * +-------+
 */
.tooltip-element-attached-bottom.tooltip-element-attached-right.tooltip-target-attached-top .tooltip-content {
  margin-bottom: $cf-tooltip-arrow-size;

  &::before {
    top: 100%;
    right: $cf-tooltip-arrow-size;
    border-top-color: $cf-tooltip-background;
  }
}

/**
 * --> +-------+
 *     |       |
 *     +-------+
 */
.tooltip-element-attached-top.tooltip-element-attached-right.tooltip-target-attached-left .tooltip-content {
  margin-right: $cf-tooltip-arrow-size;

  &::before {
    top: $cf-tooltip-arrow-size;
    left: 100%;
    border-left-color: $cf-tooltip-background;
  }
}

/**
 * +-------+ <--
 * |       |
 * +-------+
 */
.tooltip-element-attached-top.tooltip-element-attached-left.tooltip-target-attached-right .tooltip-content {
  margin-left: $cf-tooltip-arrow-size;

  &::before {
    top: $cf-tooltip-arrow-size;
    right: 100%;
    border-right-color: $cf-tooltip-background;
  }
}

/**
 *     +-------+
 *     |       |
 * --> +-------+
 */
.tooltip-element-attached-bottom.tooltip-element-attached-right.tooltip-target-attached-left .tooltip-content {
  margin-right: $cf-tooltip-arrow-size;

  &::before {
    bottom: $cf-tooltip-arrow-size;
    left: 100%;
    border-left-color: $cf-tooltip-background;
  }
}

/**
 * +-------+
 * |       |
 * +-------+ <--
 */
.tooltip-element-attached-bottom.tooltip-element-attached-left.tooltip-target-attached-right .tooltip-content {
  margin-left: $cf-tooltip-arrow-size;

  &::before {
    bottom: $cf-tooltip-arrow-size;
    right: 100%;
    border-right-color: $cf-tooltip-background;
  }
}
