/**
 * https://www.npmjs.com/package/cf-component-progress
 *
 * <div class="cf-progress">
 *   <progress class="cf-progress__bar" max="..." value="..."/>
 *   <ol class="cf-progress__items">
 *     <li class="cf-progress__item">One</li>
 *     <li class="cf-progress__item">Two</li>
 *     <li class="cf-progress__item">Three</li>
 *   </ol>
 * </div>
 */

.cf-progress {
  position: relative;
}

.cf-progress__bar {
  display: block;
  width: 100%;
  appearance: none;

  &::-webkit-progress-bar {
    background-color: $cf-progress-bar-background;
    color: $cf-progress-bar-background;
  }

  &::-webkit-progress-value {
    background-color: $cf-progress-bar-value-background;
    color: $cf-progress-bar-value-background;
    transition: width $cf-progress-bar-value-transition ease;
  }

  /* Mozilla uses progress-bar as the value */
  &::-moz-progress-bar {
    background-color: $cf-progress-bar-value-background;
    color: $cf-progress-bar-value-background;
  }
}

.cf-progress__items {
  list-style: none;
  @extend %cf-layout-clearfix;
  margin: 0;
  padding: 0;
}

.cf-progress__item {
  float: left;
  height: auto;
  padding: 0;
  padding-top: $cf-progress-item-top;

  color: $cf-progress-item-color;
  font-size: $cf-progress-item-font-size;

  text-align: center;
  cursor: pointer;

  &::before {
    content: none;
  }

  .cf-link {
    display: block;
    color: inherit;
    cursor: pointer;
  }
}

.cf-progress__item--active {
  color: $cf-progress-item-active-color;
}

.cf-progress__item--disabled {
  color: $cf-progress-item-disabled-color;
  cursor: default;
}

@media (max-width: $cf-breakpoint-tablet) {
  .cf-progress__item {
    display: none;
    width: auto !important;
    float: none;
  }

  .cf-progress__item--active {
    display: block;
  }
}
