/**
 * https://www.npmjs.com/package/cf-component-icon
 *
 * <i class="cf-icon cf-icon--help">...</i>
 * <i class="cf-icon cf-icon--gear">...</i>
 *
 * <!-- sizes -->
 * <i class="cf-icon cf-icon--* cf-icon--{2x,3x,4x,large,xlarge}">...</i>
 *
 * <!-- modifiers (can have any of these at the same time) -->
 * <i class="cf-icon cf-icon--* cf-icon--border">...</i>
 * <i class="cf-icon cf-icon--* cf-icon--spin">...</i>
 * <i class="cf-icon cf-icon--* cf-icon--muted">...</i>
 * <i class="cf-icon cf-icon--* cf-icon--white">...</i>
 */

.cf-icon {
  display: inline;
  width: auto;
  height: auto;
  margin-top: 0;

  line-height: normal;
  vertical-align: baseline;
  font-family: "cloudflare-font";
  font-weight: normal;
  font-style: normal;
  text-decoration: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: none;
  background-position: 0% 0%;
  background-repeat: repeat;

  &::before {
    display: inline-block;
    text-decoration: inherit;
    speak: none;
  }
}

.cf-icon__label {
  display: inline-block;
  width: 0;
  height: 0;
  overflow: hidden;
}

.cf-icon--border {
  border: solid 1px $cf-icon-border-color;
  padding: 0.2em 0.25em 0.15em;
  border-radius: $cf-icon-border-radius;
}

.cf-icon--spin {
  display: inline-block;
  animation: cf-icon-spin 2000ms infinite linear;
}

@keyframes cf-icon-spin {
  0%   { transform: rotate(0deg);   }
  100% { transform: rotate(359deg); }
}

.cf-icon--muted {
  color: $cf-icon-muted-color;
}

.cf-icon--white {
  color: $cf-color-white;
}

.cf-icon--2x {
  font-size: 2em;

  &.cf-icon--border {
    border-width: 2px;
  }
}

.cf-icon--3x {
  font-size: 3em;

  &.cf-icon--border {
    border-width: 3px;
  }
}

.cf-icon--4x {
  font-size: 4em;

  &.cf-icon--border {
    border-width: 4px;
  }
}

.cf-icon--large {
  vertical-align: -10%;
  font-size: $cf-icon-large-font-size;
}

.cf-icon--xlarge {
  font-size: $cf-icon-xlarge-font-size;
}

.cf-icon--caret-up::before,
.cf-icon--chevron-up::before        { content: "\f000"; }
.cf-icon--caret-down::before,
.cf-icon--chevron-down::before      { content: "\f001"; }
.cf-icon--caret-left::before,
.cf-icon--chevron-left::before      { content: "\f002"; }
.cf-icon--caret-right::before,
.cf-icon--chevron-right::before     { content: "\f003"; }
.cf-icon--ok-sign::before           { content: "\f004"; }
.cf-icon--exclamation-sign::before  { content: "\f005"; }
.cf-icon--info-sign::before         { content: "\f006"; }
.cf-icon--pause::before             { content: "\f007"; }
.cf-icon--time::before              { content: "\f008"; }
.cf-icon--ok::before                { content: "\f009"; }
.cf-icon--clipboard::before         { content: "\f00a"; }
.cf-icon--bolt::before              { content: "\f00b"; }
.cf-icon--chart::before             { content: "\f00c"; }
.cf-icon--drive::before             { content: "\f00d"; }
.cf-icon--filter::before            { content: "\f00e"; }
.cf-icon--flowchart::before         { content: "\f00f"; }
.cf-icon--hamburger::before         { content: "\f010"; }
.cf-icon--lock::before              { content: "\f011"; }
.cf-icon--list::before              { content: "\f012"; }
.cf-icon--file::before              { content: "\f013"; }
.cf-icon--shield::before            { content: "\f014"; }
.cf-icon--plus::before              { content: "\f015"; }
.cf-icon--happy::before             { content: "\f016"; }
.cf-icon--sad::before               { content: "\f017"; }
.cf-icon--search::before            { content: "\f018"; }
.cf-icon--wrench::before            { content: "\f019"; }
.cf-icon--remove::before,
.cf-icon--remove-sign::before       { content: "\f01a"; }
.cf-icon--speech::before            { content: "\f01b"; }
.cf-icon--gear::before              { content: "\f01c"; }
.cf-icon--help::before              { content: "\f01d"; }
.cf-icon--widen::before             { content: "\f022"; }
.cf-icon--calendar::before          { content: "\f01e"; }
.cf-icon--facebook::before          { content: "\f01f"; }
.cf-icon--twitter::before           { content: "\f020"; }
.cf-icon--googleplus::before        { content: "\f021"; }
.cf-icon--linkedin::before          { content: "\f022"; }
.cf-icon--resize-horizontal::before { content: "\f024"; }
.cf-icon--upload::before            { content: "\f025"; }

.cf-icon--loading {
  background: transparent url(asset-url("image", "spinner.gif")) no-repeat center;

  &::before {
    content: "";
    width: $cf-icon-size; /* width of icon */
  }
}

.cf-icon--close:before {
  content: '\00D7';
  font-family: $cf-font-family-sans-serif;
}