/**
 * https://www.npmjs.com/package/cf-component-list
 *
 * <ul class="cf-list cf-list--unstyled">
 *   <li class="cf-list__item">...</li>
 *   <li class="cf-list__item">...</li>
 * </ul>
 *
 * <ol class="cf-list cf-list--ordered">
 *   <li class="cf-list__item">...</li>
 *   <li class="cf-list__item">...</li>
 * </ol>
 */

.cf-list {
  list-style-type: disc;
  list-style-position: outside;
  margin-left: $cf-list-margin;
}

.cf-list--ordered {
  list-style-type: decimal;
}

.cf-list--unstyled {
  list-style-type: none;
  margin-left: 0;
}
