/**
 * https://www.npmjs.com/package/cf-component-link
 */

.cf-link {
  color: $cf-link-default-color;
  outline: none;
  text-decoration: none;
  transition: all 150ms ease;
  cursor: pointer;

  &:hover {
    color: $cf-link-hover-color;
  }

  &:focus {
    color: $cf-link-focus-color;
    @extend %cf-a11y-tab_focus;
  }

  &:active {
    color: $cf-link-active-color;
    outline: none;
  }
}
