/**
 * https://www.npmjs.com/package/cf-component-text
 *
 * <span class="cf-text--normal cf-text--info">...</span>
 */

/**
 * Size
 */

.cf-text--normal {
  font-size: calc(strip($cf-font-size) / $cf-rem);
  font-weight: $cf-weight-normal;
}

.cf-text--small {
  font-size: calc(12 / $cf-em);
  line-height: 1.3;
}

/**
 * Align
 */

.cf-text--start {
  text-align: left;
}

.cf-text--center {
  text-align: center;
}

.cf-text--justify {
  text-align: justify;
}

.cf-text--end {
  text-align: right;
}

/**
 * Case
 */

.cf-text--capitalize {
  text-transform: capitalize;
}

.cf-text--titlecase {
  &:first-word {
    text-transform: capitalize;
  }
}

.cf-text--lowercase {
  text-transform: lowercase;
}

.cf-text--uppercase {
  text-transform: uppercase;
}

/**
 * Colors
 */

.cf-text--info {
  color: $cf-text-info;
}

.cf-text--success {
  color: $cf-text-success;
}

.cf-text--warning {
  color: $cf-text-warning;
}

.cf-text--error {
  color: $cf-text-error;
}

.cf-text--muted {
  color: $cf-text-muted;
}
