/**
 * https://www.npmjs.com/package/cf-component-table
 */

.cf-table {
  background: white;
  border-collapse: collapse;
  border-spacing: 0;
  max-width: 100%;
  width: 100%;
  margin-bottom: $cf-table-margin-bottom;
}

.cf-table__head {
  background: $cf-table-head-background;
}

.cf-table__body + .cf-table__body {
  border-top: 2px solid $cf-table-border-color;
}

.cf-table__cell {
  border-top: 1px solid $cf-table-border-color;
  line-height: $cf-line-height;
  padding: $cf-table-padding;
  vertical-align: middle;
}

.cf-table__cell--head {
  font-weight: $cf-table-head-font-weight;

  &:first-letter {
    text-transform: capitalize;
  }
}

.cf-table__head .cf-table__cell {
  vertical-align: bottom;
}

/**
 * Row Styles
 */

.cf-table__row--info > .cf-table__cell {
  border-color: $cf-table-info-border-color;
  background: $cf-table-info-color;
}

.cf-table__row--success > .cf-table__cell {
  border-color: $cf-table-success-border-color;
  background: $cf-table-success-color;
}

.cf-table__row--warning > .cf-table__cell {
  border-color: $cf-table-warning-border-color;
  background: $cf-table-warning-color;
}

.cf-table__row--error > .cf-table__cell {
  border-color: $cf-table-error-border-color;
  background: $cf-table-error-color;
}

/**
 * Mobile Styles
 */

@media (max-width: $cf-breakpoint-tablet) {
  .cf-table,
  .cf-table__head,
  .cf-table__body,
  .cf-table__foot,
  .cf-table__row,
  .cf-table__cell {
    display: block;
    width: 100%;
    box-sizing: border-box;
  }

  .cf-table__cell {
    border-top-width: 0;
  }

  .cf-table__cell:first-child {
    border-top: 1px solid $cf-table-border-color;
  }
}

/**
 * Condensed
 */

.cf-table--condensed {
  .cf-table__cell {
    padding: $cf-table-condensed-padding;
  }
}

/**
 * Striped
 */

.cf-table--striped .cf-table__body > {
  .cf-table__row--default:nth-child(odd) > .cf-table__cell {
    background: $cf-table-striped-default-color;
  }

  .cf-table__row--info:nth-child(odd) > .cf-table__cell {
    background: $cf-table-striped-info-color;
  }

  .cf-table__row--success:nth-child(odd) > .cf-table__cell {
    background: $cf-table-striped-success-color;
  }

  .cf-table__row--warning:nth-child(odd) > .cf-table__cell {
    background: $cf-table-striped-warning-color;
  }

  .cf-table__row--error:nth-child(odd) > .cf-table__cell {
    background: $cf-table-striped-error-color;
  }
}

/**
 * Hover
 */

.cf-table--hover .cf-table__body > {
  .cf-table__row--default:hover > .cf-table__cell {
    background: $cf-table-hover-default-color;
  }

  .cf-table__row--info:hover > .cf-table__cell {
    background: $cf-table-hover-info-color;
  }

  .cf-table__row--success:hover > .cf-table__cell {
    background: $cf-table-hover-success-color;
  }

  .cf-table__row--warning:hover > .cf-table__cell {
    background: $cf-table-hover-warning-color;
  }

  .cf-table__row--error:hover > .cf-table__cell {
    background: $cf-table-hover-error-color;
  }
}

/**
 * Bordered
 */

.cf-table--bordered {
  border: 1px solid $cf-table-border-color;
  border-collapse: separate; /* Done so we can round those corners! */
  *border-collapse: collapse; /* IE7 can't round corners anyway */
  border-left: 0;

  .cf-table__cell {
    border-left: 1px solid $cf-table-border-color;
  }

  /* Prevent a double border */
  .cf-table__head,
  .cf-table__body {
    &:first-child .cf-table__row:first-child .cf-table__cell {
      border-top: 0;
    }
  }
}
