/**
 * https://www.npmjs.com/package/cf-component-flex
 *
 * <div class="cf-flex>
 *   <div class="cf-flex__item">...</div>
 *   <div class="cf-flex__item">...</div>
 *   <div class="cf-flex__item cf-flex__item--collapse">...</div>
 * </div>
 */

.cf-flex--thin .cf-flex__item + .cf-flex__item {
  margin-top: $cf-flex-spacing-thin;
}

.cf-flex--wide .cf-flex__item + .cf-flex__item {
  margin-top: $cf-flex-spacing-wide;
}

@media (min-width: $cf-breakpoint-tablet) {
  .cf-flex {
    display: flex;
  }

  .cf-flex__item {
    flex: 1 0;
  }

  .cf-flex__item--collapse {
    flex: 0 0;
  }

  .cf-flex--thin .cf-flex__item + .cf-flex__item {
    margin-top: 0;
    margin-left: $cf-flex-spacing-thin;
  }

  .cf-flex--wide .cf-flex__item + .cf-flex__item {
    margin-top: 0;
    margin-left: $cf-flex-spacing-wide;
  }
}
