/**
 * https://www.npmjs.com/package/cf-component-toggle
 *
 * <label className="cf-toggle cf-toggle--active" for="...">
 *   <input class="cf-toggle__checkbox" type="checkbox" id="..."/>
 *   <span class="cf-toggle__label">Label</span>
 *   <span class="cf-toggle__handle"></span>
 * </label>
 */

.cf-toggle {
  display: block;
  position: relative;
  height: $cf-toggle-height;
  width: $cf-toggle-width;
  border-radius: $cf-toggle-border-radius;
  border: $cf-toggle-border;
  cursor: pointer;

  &::before,
  &::after {
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 50%;
    line-height: $cf-toggle-height;
    text-align: center;
  }

  &::before {
    content: "On";
    left: 0;
    background: $cf-toggle-on-background;
    color: $cf-toggle-on-color;
  }

  &::after {
    content: "Off";
    right: 0;
    background: $cf-toggle-off-background;
    color: $cf-toggle-off-color;
  }
}

.cf-toggle__checkbox,
.cf-toggle__label {
  @extend %cf-a11y-sr_only;
}

.cf-toggle__handle {
  display: block;
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  width: 50%;

  border: inherit;
  border-width: 0;
  border-right-width: inherit;

  background: $cf-toggle-handle-background;
  transition: left 100ms ease;
}

.cf-toggle--active .cf-toggle__handle {
  border-right-width: 0;
  border-left-width: inherit;
  left: 50%;
}

.cf-toggle__checkbox:focus ~ .cf-toggle__handle {
  @extend %cf-a11y-tab_focus;
}
