/**
 * https://www.npmjs.com/package/cf-component-notifications
 *
 * <div class="cf-notifications__global_container">
 *   <div class="cf-notifications">
 *     <div class="cf-notifications__item">
 *       <span class="cf-notifications__item_progress"></span>
 *       <span class="cf-notifications__item_close"></span>
 *       <span class="cf-notifications__item_message">...</span>
 *     </div>
 *     ...
 *   </div>
 * </div>
 */

.cf-notifications {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.cf-notifications__item {
  position: relative;
  padding: $cf-notifications-padding-vertical $cf-notifications-padding-horizontal;
  padding-right: 2.5rem;
  border: 1px solid;
  font-size: $cf-notifications-font-size;
  vertical-align: middle;
  -webkit-text-stroke: 0;
  border-radius: $cf-notifications-border-radius;
  cursor: pointer;
  -webkit-font-smoothing: antialiased;

  &::before {
    font-family: "cloudflare-font";
    font-size: 1.25rem;
    position: absolute;
    top: 50%;
    left: 0.75rem;
    transform: translate(0, -50%);
    line-height: 1;
    vertical-align: middle;
  }

  & + & {
    margin-top: 2px;
  }
}

/**
 * Global Container
 */

.cf-notifications__global_container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  max-height: 25%;
  overflow: auto;
  z-index: $cf-zindex-notifications-global;

  .cf-notifications {
    margin-top: 0;
    margin-bottom: 0;
  }

  .cf-notifications__item {
    border-radius: 0;
    border-left-width: 0;
    border-right-width: 0;
    transform-origin: bottom left;
    animation: cf-notifications-slide-up 200ms ease-in-out;

    &::before {
      animation: cf-notifications-slide-from-left 200ms ease-out;
    }
  }

  .cf-notifications__item + .cf-notifications__item {
    margin-top: 0;
    border-top-width: 0;
  }
}

/**
 * Elements
 */

.cf-notifications__item_progress {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  background: $cf-notifications-progress-background;
}

.cf-notifications__item_progress--active {
  width: 0;
  transition: width 0s linear;
}

.cf-notifications__item_close {
  position: absolute;
  z-index: 3;
  top: 0;
  right: 0;
  bottom: 0;
  width: 2.5em;
  cursor: pointer;

  &:hover {
    background: $cf-notifications-close-hover-background;
  }

  &::before {
    content: '\00D7';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.25rem;
  }
}

.cf-notifications__item_message {
  position: relative;
  z-index: 2;
}

/**
 * Themes
 */

.cf-notifications__item--success {
  background-color: $cf-notifications-success-background;
  border-color: $cf-notifications-success-border;
  color: $cf-notifications-success-color;

  &::before {
    content: '\f004';
  }
}

.cf-notifications__item--error {
  background-color: $cf-notifications-error-background;
  border-color: $cf-notifications-error-border;
  color: $cf-notifications-error-color;

  &::before {
    content: '\f005';
  }
}

.cf-notifications__item--info {
  background-color: $cf-notifications-info-background;
  border-color: $cf-notifications-info-border;
  color: $cf-notifications-info-color;

  &::before {
    content: '\f006';
  }
}

.cf-notifications__item--warning {
  background-color: $cf-notifications-warning-background;
  border-color: $cf-notifications-warning-border;
  color: $cf-notifications-warning-color;

  &::before {
    content: '\f005';
  }
}

/**
 * Animations
 */

 @keyframes cf-notifications-slide-up {
   0% {
     transform: scaleY(0);
   }
   100% {
     transform: scaleY(1);
   }
 }

 @keyframes cf-notifications-slide-from-left {
   0% {
     transform: translateX(-75px);
   }
   100% {
     transform: translateX(0);
   }
 }
