/**
 * https://www.npmjs.com/package/cf-component-radio
 *
 * <div class="cf-radio__group">
 *   <label class="cf-radio cf-radio--checked">
 *     <input type="radio" class="cf-radio__input"/>
 *     <!-- note that the label is optional -->
 *     <span class="cf-radio__label">One</span>
 *   </label>
 *   <label class="cf-radio">
 *     <input type="radio" class="cf-radio__input"/>
 *   </label>
 * </div>
 */

.cf-radio__group {
  @extend %cf-control__radio_group;
}

.cf-radio {
  @extend %cf-control__radio;
}

.cf-radio__input {
  @extend %cf-control__radio_input;
}

.cf-radio--checked .cf-radio__input {
  @extend %cf-control__radio_input--checked;
}

.cf-radio__label {
  @extend %cf-control__radio_label;
}

.cf-radio__group {
  .cf-radio:first-child {
    margin-top: 0;
  }

  .cf-radio {
    margin-top: $cf-control-group-margin-top;
  }
}
