/**
 * https://www.npmjs.com/package/cf-component-dropdown
 *
 * <!-- Be conscious about dropdowns not necessarily being inside a button group. -->
 * <div class="cfn-btn__group">
 *   <button class="cf-btn cf-btn--default">...</button>
 *
 *   <ul class="cf-dropdown cf-dropdown--left" role="menu">
 *     <li class="cf-dropdown__link" role="menuitem">
 *       <a href="..." class="cf-link">...</a>
 *     </li>
 *     <div class="cf-dropdown__separator" role="separator"></div>
 *     <li class="cf-dropdown__link" role="menuitem">
 *       <a href="..." class="cf-link">...</a>
 *     </li>
 *   </ul>
 * </div>
 */

.cf-dropdown {
  position: absolute;
  z-index: 1;
  min-width: $cf-dropdown-width;
  margin: $cf-dropdown-top 0 0;
  padding: $cf-dropdown-padding;
  list-style: none;
  background: $cf-dropdown-background;
  border: $cf-dropdown-border;
  border-radius: $cf-dropdown-border-radius;
  box-shadow: $cf-dropdown-box-shadow;
  animation: cf-dropdown-fade-slide $cf-dropdown-animation-speed ease-out;

  &::before {
    content: "";
    display: block;
    position: absolute;
    bottom: 100%;
    border: solid transparent;
    border-width: $cf-dropdown-arrow-size;
    border-top-width: 0;
    border-bottom-color: $cf-dropdown-arrow-color;
  }
}

@keyframes cf-dropdown-fade-slide {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
    top: 80%;
  }
  100% {
    display: none;
    opacity: 1;
    top: 102%;
  }
}

.cf-dropdown--left {
  left: 0;

  &::before {
    left: 10px;
  }
}

.cf-dropdown--right {
  right: 0;

  &::before {
    right: 10px;
  }
}

.cf-dropdown__link {
  position: relative;
  z-index: 1;

  a {
    display: block;
    padding: $cf-dropdown-link-padding;
    cursor: pointer;
    white-space: nowrap;

    &:hover {
      color: $cf-dropdown-link-hover-color;
      background: $cf-dropdown-link-hover-background;
    }

    &:focus {
      color: $cf-dropdown-link-focus-color;
      background: $cf-dropdown-link-focus-background;
      @extend %cf-a11y-tab_focus;
    }

    &:active {
      color: $cf-dropdown-link-active-color;
      background: $cf-dropdown-link-active-background;
    }
  }

  &:first-child {
    border-top-width: 0;
  }
}

.cf-dropdown__separator {
  margin: $cf-dropdown-separator-margin;
  border-top: $cf-dropdown-separator-height solid $cf-dropdown-separator-color;
}
