/**
 * https://www.npmjs.com/package/cf-component-code
 *
 * <code class="cf-code">...</code>
 *
 * <pre class="cf-code__block">
 *   <code class="cf-code">...</code>
 * </pre>
 */

.cf-code,
.cf-code__block {
  background-color: $cf-code-background;
  border: 1px solid $cf-code-border;
  font-family: $cf-code-font;
  border-radius: $cf-code-border-radius;
}

.cf-code {
  display: inline-block;
  margin-left: calc(2 / $cf-em);
  margin-right: calc(2 / $cf-em);
  padding: 0 calc(6 / $cf-em);
  vertical-align: baseline;
  color: $cf-code-color;
}

.cf-code__block {
  display: block;
  width: 100%;
  margin: $cf-code-block-margin 0;
  padding: $cf-code-block-padding;
  font-size: $cf-code-font-size;
  color: $cf-code-block-color;

  .cf-code {
    display: inherit;
    margin: auto;
    padding: auto;
    background: none;
    border: none;
    color: inherit;
  }
}
