/**
 * https://www.npmjs.com/package/cf-component-card
 *
 * <section class="cf-card">
 *   <div class="cf-card__section cf-card__section--error">
 *     <div class="cf-card__content">
 *       <h3 class="cf-card__heading">...</h3>
 *       ...
 *       <div class="cf-card__messages">
 *         <div role="alert" class="cf-card__message cf-card__message--info">
 *           ...
 *         </div>
 *         <div role="alert" class="cf-card__message cf-card__message--error">
 *           ...
 *         </div>
 *       </div>
 *     </div>
 *     <div class="cf-card__control">
 *       ...
 *     </div>
 *   </div>
 *   <div class="cf-card__section cf-card__section--default">
 *     <div class="cf-card__toolbar">
 *       <div class="cf-card__toolbar_controls">
 *         ...
 *       </div>
 *       <div class="cf-card__toolbar_links">
 *         <a href="#" role="tab" id="..." class="cf-link cf-card__toolbar_link cf-card__toolbar_link--open">...</a>
 *         <a href="#" role="tab" id="..." class="cf-link cf-card__toolbar_link">...</a>
 *       </div>
 *     </div>
 *     <div class="cf-card__drawers_container cf-card__drawers_container--open">
 *       <div role="tabpanel" class="cf-card__drawer cf-card__drawer--active" aria-labelledby="..." aria-hidden="false">
 *         ...
 *       </div>
 *       <div role="tabpanel" class="cf-card__drawer" aria-labelledby="..." aria-hidden="true">
 *         ...
 *       </div>
 *     </div>
 *   </div>
 * </section>
 */

.cf-card {
  margin-top: $cf-card-margin;
  margin-bottom: $cf-card-margin;
  padding: 0;
  border: 1px solid $cf-card-border-color;
  background-color: $cf-card-background;

  border-radius: $cf-card-border-radius;
  transition: all 0.35s ease;
}

.cf-card__section {
  display: table;
  table-layout: fixed;
  width: 100%;

  & + & {
    border-top: 1px solid $cf-card-border-color;
  }
}

.cf-card__section--default {
  /* ... */
}

.cf-card__section--error {
  border-left: 3px solid $cf-color-red;
}

.cf-card__heading {
  font-weight: $cf-weight-normal;
  margin-bottom: calc(strip($cf-font-size) / $cf-rem);
}

.cf-card__messages {
  padding-top: calc($cf-card-margin / 2);

  /* Fix for items beneath .card__messages */
  .card__messages + p {
    margin-top: 1em;
  }
}

.cf-card__message {
  /* ... */
}

.cf-card__message--info {
  /* ... */
}

.cf-card__message--success {
  /* ... */
}

.cf-card__message--error {
  /* ... */
}

.cf-card__message--warning {
  /* ... */
}

.cf-card__content {
  padding: $cf-card-padding;
}

.cf-card__control {
  position: relative;
  padding: calc((strip($cf-font-size) * 2) / $cf-rem);
  border: 0 solid $cf-card-border-color;
  border-top-width: 1px;
  background-color: $cf-card-control-background;
  text-align: center;
}

@media (min-width: $cf-breakpoint-tablet) {
  .cf-card__content,
  .cf-card__control {
    display: table-cell;
  }

  .cf-card__control {
    width: 40%;
    border-left-width: 1px;
    border-top-width: 0;
    vertical-align: middle;
  }
}

@media (min-width: $cf-breakpoint-desktop) {
  .cf-card__control {
    width: 30%;
  }
}

.cf-card__toolbar {
  min-height: $cf-card-toolbar-height;
  @extend %cf-layout-clearfix;
}

.cf-card__toolbar_controls {
  /* ... */
}

.cf-card__toolbar_links {
  float: right;
}

.cf-card__toolbar_link {
  position: relative;
  display: inline-block;
  padding: $cf-card-toolbar-padding;

  &:after {
    content: "\f003";
    font-family: "cloudflare-font";
    font-style: normal;
    font-weight: normal;
    margin-top: -0.6em;

    position: absolute;
    top: 50%;
    right: $cf-card-toolbar-link-icon-offset;
    transition: transform 0.2s ease;
  }
}

.cf-card__toolbar_link--open:after {
  transform: rotate(90deg);
}

.cf-card__drawers_container {
  display: none;
}

.cf-card__drawers_container--open {
  display: block;
}

.cf-card__drawer {
  /* ... */
}

.cf-card__drawer--active {
  border-top: 1px solid $cf-card-border-color;
  padding: $cf-card-padding;
}
