/**
 * Fonts
 */

@font-face {
  font-family: "cloudflare-font";
  src: url(asset-url("font", "cloudflare-font.eot"));
  src: url(asset-url("font", "cloudflare-font.eot?#iefix"))          format("embedded-opentype"),
       url(asset-url("font", "cloudflare-font.woff"))                format("woff"),
       url(asset-url("font", "cloudflare-font.ttf"))                 format("truetype"),
       url(asset-url("font", "cloudflare-font.svg#cloudflare-font")) format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans";
  src: url(asset-url("font", "opensans-300.eot"));
  src: url(asset-url("font", "opensans-300.eot?#iefix")) format("embedded-opentype"),
       url(asset-url("font", "opensans-300.woff2"))      format("woff2"),
       url(asset-url("font", "opensans-300.woff"))       format("woff"),
       url(asset-url("font", "opensans-300.ttf"))        format("truetype");
  font-weight: 300;
  font-style: normal;
}

