/**
 * https://www.npmjs.com/package/cf-component-checkbox
 *
 * <label for="..." class="cf-checkbox cf-checkbox--checked">
 *   <input type="checkbox" class="cf-checkbox__input">
 *
 *   <!-- This element is optional -->
 *   <span class="cf-checkbox__label">
 *     ...
 *   </span>
 * </label>
 */

.cf-checkbox__group {
  @extend %cf-control__checkbox_group;
}

.cf-checkbox {
  @extend %cf-control__checkbox;
}

.cf-checkbox__input {
  @extend %cf-control__checkbox_input;
}

.cf-checkbox--checked .cf-checkbox__input {
  @extend %cf-control__checkbox_input--checked;
}

.cf-checkbox__label {
  @extend %cf-control__checkbox_label;
}

.cf-checkbox__group {
  .cf-checkbox:first-child {
    margin-top: 0;
  }

  .cf-checkbox {
    margin-top: $cf-control-group-margin-top;
  }
}