/**
 * a11y Helpers
 */

%cf-a11y-tab_focus {
  /* Default */
  outline: thin dotted;
  /* WebKit */
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

%cf-a11y-sr_only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0
}

