/**
 * Control Helpers
 */

/* Generic styles which apply to all input elements. */
%cf-control__text_input,
%cf-control__textarea_input,
%cf-control__checkbox_input,
%cf-control__radio_input {
  z-index: 0;
  max-width: 100%;
  margin: 0 0 $cf-control-margin-bottom;
  padding: $cf-control-padding;
  border: $cf-control-border;

  vertical-align: middle;
  font-family: $cf-control-font-family;
  font-size: $cf-control-font-size;

  background: $cf-control-background;
  color: $cf-form-color;
  outline: none;

  transition: border-color $cf-control-transition-speed ease;

  &:hover {
    border-color: $cf-form-hover-border;
  }

  &:focus {
    border-color: $cf-control-focus-border-color;
    @extend %cf-a11y-tab_focus;
  }
}

%cf-control__text_input,
%cf-control__textarea_input {
  border-radius: $cf-control-text-border-radius;
}

%cf-control__checkbox_group,
%cf-control__radio_group {
  text-align: left;
  display: inline-block;
  vertical-align: middle;
}

%cf-control__checkbox,
%cf-control__radio {
  cursor: pointer;
  display: block;
  min-height: $cf-control-box-min-height;
  padding-left: calc((strip($cf-font-size) * 2) / $cf-em);

  &:hover input {
    border-color: $cf-form-hover-border;
  }
}

%cf-control__checkbox_input,
%cf-control__radio_input {
  position: relative;
  height: $cf-control-box-input-size;
  width: $cf-control-box-input-size;
  margin: 1px 0 0;
  padding: 0;
  line-height: normal;
  appearance: none;
  top: -1px;

  &::before {
    content: '';
    position: absolute;
    background-color: transparent;
    color: transparent;
    -webkit-text-stroke: 0;
    transition: all $cf-control-box-transition-speed ease-out;
  }
}

%cf-control__checkbox_input {
  border-radius: $cf-control-checkbox-border-radius;

  &:before {
    content: '\f009';
    font-family: 'cloudflare-font';
    font-size: $cf-control-box-input-size;
    left: -1px;
    top: -1px;
  }
}

%cf-control__checkbox_input--checked {
  &:before {
    color: $cf-control-box-check-color;
  }
}

%cf-control__radio_input {
  &,
  &:before {
    border-radius: $cf-control-radio-border-radius;
  }

  &:before {
    height: 3px;
    width: 3px;
    top: 5px;
    left: 5px;
  }
}

%cf-control__radio_input--checked {
  &:before {
    background-color: $cf-control-box-check-color;
    height: 7px;
    width: 7px;
    top: 3px;
    left: 3px;
  }
}

%cf-control__checkbox_label,
%cf-control__radio_label {
  display: inline;
  font-size: $cf-control-box-label-font-size;
  margin-bottom: $cf-control-box-label-bottom;
  margin-left: $cf-control-box-label-left;
  min-height: $cf-control-box-label-min-height;
}
