/**
 * https://www.npmjs.com/package/cf-component-heading
 *
 * <h1 class="cf-heading cf-heading--1">
 *   Title
 *   <small class="cf-heading__caption">
 *     Caption
 *   </small>
 * </h1>
 */

.cf-heading {
  font-weight: $cf-heading-font-weight;
}

.cf-heading__caption {
  color: $cf-heading-caption-color;
  font-size: 80%;
  margin-left: 0.5em;
}

.cf-heading--1 {
  font-size: $cf-heading-1-font-size;
  line-height: $cf-heading-1-line-height;
  font-weight: 400;
}

.cf-heading--2 {
  font-size: $cf-heading-2-font-size;
  line-height: $cf-heading-2-line-height;
}

.cf-heading--3 {
  font-size: $cf-heading-3-font-size;
  line-height: $cf-heading-3-line-height;
}

.cf-heading--4 {
  font-size: $cf-heading-4-font-size;
  line-height: $cf-heading-4-line-height;
}

.cf-heading--5 {
  font-size: $cf-heading-5-font-size;
  line-height: $cf-heading-5-line-height;
}

.cf-heading--6 {
  font-size: $cf-heading-6-font-size;
  line-height: $cf-heading-6-line-height;
}
