/**
 * https://www.npmjs.com/package/cf-component-callout
 *
 * <div class="cf-callout cf-callout-default">
 *   <p class="cf-callout__heading">Heading</p>
 *   <p class="cf-callout__content">Content</p>
 * </div>
 */

.cf-callout {
  margin: $cf-callout-spacing;
  padding: $cf-callout-padding;
  border: 1px solid transparent;
  border-radius: $cf-callout-border-radius;
}

.cf-callout--default {
  background-color: $cf-callout-default-background;
  border-color: $cf-callout-default-border;
  color: $cf-callout-default-color;
}

.cf-callout--info {
  background-color: $cf-callout-info-background;
  border-color: $cf-callout-info-border;
  color: $cf-callout-info-color;
}

.cf-callout__heading {
   /* ... */
}

.cf-callout__content {
  margin-top: 0;
}
